}

html {
  font-family: 'Josefin Sans', sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style-type: none;
}

button{
  border: none;
  background-color: white;
}

/* ========= GENERAL RULES ========= */

:root {
  --main-bg-color: #d7edfd;
  --purple: #6a75e0;
}


.purpletitle{
  color: #6a75e0;
  color: var(--purple);
}

.interlignSmall{
  line-height: 20px;
}

.pointer{
  cursor: pointer;
}

/* ========= FORMS ========= */

input {
  width: 250px;
  background: white;
  color: #6a75e0;
  color: var(--purple);
  border: 1px solid #6a75e0;
  border: 1px solid var(--purple);
  border-radius: 50px;
  padding: 10px 5px;
  margin: 10px 0px;
}

.containerform{
  border: solid 1px #6a75e0;
  border: solid 1px var(--purple); 
  border-radius: 20px;
  padding: 30px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column wrap;
          flex-flow: column wrap;
  margin-bottom: 40px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  width: -webkit-max-content;
  width: max-content;
}



.submit{
  background: #6a75e0;
  background: var(--purple);
  color: white;
  border-radius: 50px;
  width: -webkit-max-content;
  width: max-content;
  padding: 10px 45px;
  font-size: 14px;
  margin-top: 20px;
}

